import { Container, Row, Col, Form, Button, Table, Tab, Tabs, Carousel } from 'react-bootstrap';
import { useParams } from 'react-router-dom';

function Detail(props) {
    let { id } =useParams();
    let selproduct=props.bestphone.find((x)=>x.id==id);

    return (
        <>
            <Container className="detailpage">
                <Row className="detailpage1">
                    <Col md={6}>
                        <img src={'/img/best' + (Number(id)+1)+'.jpg'} alt="best" style={{ width: '100%' }} />
                    </Col>
                    <Col md={6}>
                        <h2>{selproduct.title}</h2>
                        <Table striped>
                            <tbody>
                                <tr>
                                    <th></th>
                                    <td>{selproduct.price}</td>
                                </tr>
                                <tr>
                                    <th></th>
                                    <td>{selproduct.percent}</td>
                                </tr>
                                <tr>
                                    <th></th>
                                    <td>{selproduct.discount.toLocaleString()}</td>
                                </tr>
                                <tr>
                                    <th></th>
                                    <td>3,000(20,000    )</td>
                                </tr>
                            </tbody>
                        </Table>
                        <Form.Group className="mb-3" >
                            <Form.Label></Form.Label>
                            <Form.Select style={{fontSize:'1.6rem'}}>
                                <option>[]  </option>
                                <option disabled>-------------------</option>
                                {
                                    props.design1.iphone.map((v,i)=>{
                                        return(
                                            <option i={i}>{props.design1.iphone[i]}</option>
                                        )
                                    })
                                }
                                <option disabled></option>
                                <option disabled>---------------------</option>
                                {
                                    props.design1.galaxy.map((v,i)=>{
                                        return(
                                            <option i={i}>{props.design1.galaxy[i]}</option>
                                        )
                                    })
                                }
                            </Form.Select>
                        </Form.Group>
                        <hr/>
                        <div className="detailbtnbox">
                            <Button variant="dark"></Button>
                            <Button variant="light"></Button>
                        </div>
                        <Row className="another">
                            <Col md={4} xs={4}>
                                <img src={selproduct.imgUrl1}/>
                                <h4>{selproduct.title1}</h4>
                                <p>{selproduct.price1.toLocaleString()}</p>
                            </Col>
                            <Col md={4} xs={4}>
                                <img src={selproduct.imgUrl2}/>
                                <h4>{selproduct.title2}</h4>
                                <p>{selproduct.price2.toLocaleString()}</p>
                            </Col>
                            <Col md={4} xs={4}>
                                <img src={selproduct.imgUrl3}/>
                                <h4>{selproduct.title3}</h4>
                                <p>{selproduct.price3.toLocaleString()}</p>
                            </Col>
                        </Row>
                    </Col>
                </Row>
                <div className="detailpage2">
                    <Tabs
                        defaultActiveKey="imformation"
                        id="uncontrolled-tab-example"
                        className="mb-3">
                        <Tab eventKey="imformation" title="">
                            <img src={'/img/best' + (Number(id) + 1) + '-1.jpg'} alt="best" style={{ width: '100%', paddingTop:'30px' }} />
                        </Tab>
                        <Tab eventKey="important" title="  ">
                            <h1>  </h1>
                            <div className="money">
                                <h3></h3>
                                <Table>
                                    <tbody>
                                        <tr>
                                            <th></th>
                                            <td>
                                                <ul>
                                                    <li>-      PC, ,       .</li>
                                                    <li>-        ,       ,     .</li>
                                                    <li>- 3         .</li>
                                                </ul>
                                            </td>
                                        </tr>
                                    </tbody>
                                </Table>
                            </div>
                            <div className="delivery">
                                <h3></h3>
                                <Table>
                                    <tbody>
                                        <tr>
                                            <th></th>
                                            <td></td>
                                        </tr>
                                        <tr>
                                            <th></th>
                                            <td></td>
                                        </tr>
                                        <tr>
                                            <th></th>
                                            <td>3,000</td>
                                        </tr>
                                        <tr>
                                            <th> </th>
                                            <td>2 ~ 7</td>
                                        </tr>
                                        <tr>
                                            <th> </th>
                                            <td>
                                                <ul>
                                                    <li>-     ,      2~4   </li>
                                                    <li>-           .<br/>     </li>
                                                </ul>
                                            </td>
                                        </tr>
                                    </tbody>
                                </Table>
                            </div>
                            <div className="change">
                                <h3>/</h3>
                                <Table>
                                    <tbody>
                                        <tr>
                                            <th></th>
                                            <td>
                                                <ul>
                                                    <span>    </span>
                                                    <li>*         </li>
                                                    <li>*               .</li>
                                                    <li>*  7 /      .</li>
                                                    <li>*         .</li>
                                                    <li>*               .</li>
                                                </ul>
                                                <ul>
                                                    <span>    </span>
                                                    <li>*      </li>
                                                    <li>*          </li>
                                                    <li>* //   7     .</li>
                                                </ul>
                                            </td>
                                        </tr>
                                    </tbody>
                                </Table>
                            </div>
                        </Tab>
                        <Tab eventKey="review" title="">
                            <div className="review">
                                <h1> </h1>
                                <hr/>
                                <Container>
                                    <Row className="graphbox">
                                        <Col lg={3} md={3}>
                                            <p></p>
                                            <p><span></span> <b>5.0</b> / 5.0</p>
                                            <p><span>5 </span>  </p>
                                            <button> </button>
                                        </Col>
                                        <Col lg={3} md={3} id="bordereffect">
                                            <div className="allgraph">
                                                <div className="graygraph">
                                                    <div className="bkgraph"></div>
                                                </div>
                                                <span>5</span>
                                                <span>100%</span>
                                            </div>
                                            <div className="allgraph">
                                                <div className="graygraph"></div>
                                                <span>4</span>
                                                <span>0%</span>
                                            </div>
                                            <div className="allgraph">
                                                <div className="graygraph"></div>
                                                <span>3</span>
                                                <span>0%</span>
                                            </div>
                                            <div className="allgraph">
                                                <div className="graygraph"></div>
                                                <span>2</span>
                                                <span>0%</span>
                                            </div>
                                            <div className="allgraph">
                                                <div className="graygraph"></div>
                                                <span>1</span>
                                                <span>0%</span>
                                            </div>
                                        </Col>
                                    </Row>
                                    <Row className="writereviewbox">
                                        <Col md={2}  sm={3} xs={4}className="writereview_left">
                                            <p></p>
                                            <p>NAME : ***</p>
                                            <p>2023-03-09</p>
                                        </Col>
                                        <Col md={10} sm={9} xs={8}>
                                            <p> !</p>
                                        </Col>
                                    </Row>
                                    <Row className="writereviewbox">
                                        <Col md={2}  sm={3} xs={4}className="writereview_left">
                                            <p></p>
                                            <p>NAME : ***</p>
                                            <p>2023-03-09</p>
                                        </Col>
                                        <Col md={10} sm={9} xs={8}>
                                            <p>!!!     !</p>
                                        </Col>
                                    </Row>
                                    <Row className="writereviewbox">
                                        <Col md={2}  sm={3} xs={4}className="writereview_left">
                                            <p></p>
                                            <p>NAME : ***</p>
                                            <p>2023-03-08</p>
                                        </Col>
                                        <Col md={10} sm={9} xs={8}>
                                            <p>   ~!  </p>
                                        </Col>
                                    </Row>
                                    <Row className="writereviewbox">
                                        <Col md={2}  sm={3} xs={4}className="writereview_left">
                                            <p></p>
                                            <p>NAME : ***</p>
                                            <p>2023-03-08</p>
                                        </Col>
                                        <Col md={10} sm={9} xs={8}>
                                            <p> !</p>
                                        </Col>
                                    </Row>
                                    <Row className="writereviewbox">
                                        <Col md={2}  sm={3} xs={4}className="writereview_left">
                                            <p></p>
                                            <p>NAME : ***</p>
                                            <p>2023-03-07</p>
                                        </Col>
                                        <Col md={10} sm={9} xs={8}>
                                            <p>    !</p>
                                        </Col>
                                    </Row>
                                </Container>
                            </div>
                        </Tab>
                    </Tabs>
                </div>
            </Container>
        </>
    )
}

export default Detail;
